import React from "react";

import HeaderCarousel from "./Carousel";
import TokenCard from "./TokenCard";
import useWindowDimensions from "../hooks/useWindowDimensions";

// import antimatterContainerImage from "../assets/antimatter_container.png";
// import matterContainerImage from "../assets/matter_container.png";
import energyImage from "../assets/energy.png";
import matterImage from "../assets/matter.png";
import antimatterImage from "../assets/antimatter.png";
import energyIcon from "../assets/energy.jpg";
import matterIcon from "../assets/matter.jpg";
import antimatterIcon from "../assets/antimatter.jpg";
import energyCardImage from "../assets/energy_card.png";
import matterCardImage from "../assets/matter_card.png";
import antimatterCardImage from "../assets/antimatter_card.png";
import { Container, Grid } from "@mui/material";
import Navigation from "./Navigation";
import contracts from "../contracts";

import Matter from "../artifacts/contracts/Matter.sol/Matter.json";
import AntiMatter from "../artifacts/contracts/AntiMatter.sol/AntiMatter.json";
import Energy from "../artifacts/contracts/Energy.sol/Energy.json";
import Countdown from "./Countdown";

const MatterCard = () => <TokenCard tokenAddress={contracts.matter} tokenAbi={Matter.abi} tokenImage={matterImage} tokenIcon={matterIcon} cardImage={matterCardImage} />;

const AntimatterCard = () => (
  <TokenCard tokenAddress={contracts.antimatter} tokenAbi={AntiMatter.abi} tokenImage={antimatterImage} tokenIcon={antimatterIcon} cardImage={antimatterCardImage} />
);

const EnergyCard = () => <TokenCard tokenAddress={contracts.energy} tokenAbi={Energy.abi} tokenImage={energyImage} tokenIcon={energyIcon} cardImage={energyCardImage} />;

const Section = (props) => (
  <Grid container id={props.id} styles={{ minHeight: props.height }} direction="row" justifyContent="space-around" alignItems="center" spacing={8}>
    {props.children}
  </Grid>
);
export default function DenseAppBar() {
  const { height } = useWindowDimensions();

  const sectionStyles = { minHeight: height };

  return (
    <div>
      <Navigation />
      <HeaderCarousel />
      <Container>
        <Grid container style={sectionStyles} id="history"></Grid>
        <Grid container style={sectionStyles} id="countdown" alignItems="center">
          <Countdown />
        </Grid>
        <Grid container style={sectionStyles} id="tokens" direction="row" justifyContent="space-around" alignItems="center" spacing={8}>
          <Grid item xs={12} md={4}>
            <MatterCard />
          </Grid>
          <Grid item xs={12} md={4}>
            <AntimatterCard />
          </Grid>
          <Grid item xs={12} md={4}>
            <EnergyCard />
          </Grid>
        </Grid>
        <Grid container style={sectionStyles} id="buy"></Grid>
        <Grid container style={sectionStyles} id="about"></Grid>
      </Container>
    </div>
  );
}
