import { Button, Card, Grid, Typography } from "@mui/material";
import { makeStyles } from "@mui/styles";
import React, { Component, useEffect, useState } from "react";
import classnames from "classnames";
import { ethers } from "ethers";

import Matter from "../artifacts/contracts/Matter.sol/Matter.json";
import AntiMatter from "../artifacts/contracts/AntiMatter.sol/AntiMatter.json";
import contracts from "../contracts";

class Clock extends Component {
  constructor(props) {
    super(props);
    this.state = { days: 0, hours: 0, minutes: 0, seconds: 0, time: 0 };
  }
  componentWillMount() {
    this.getTimeUntil(this.props.deadline);
  }
  componentDidMount() {
    setInterval(() => this.getTimeUntil(this.props.deadline), 1000);
  }
  leading0(num) {
    return num < 10 ? "0" + num : num;
  }
  getTimeUntil(deadline) {
    if (typeof deadline === "string") {
      deadline = Date.parse(deadline);
    }
    const time = deadline - Date.parse(new Date());
    if (time < 0) {
      this.setState({ days: 0, hours: 0, minutes: 0, seconds: 0, time: 0 });
    } else {
      const seconds = Math.floor((time / 1000) % 60);
      const minutes = Math.floor((time / 1000 / 60) % 60);
      const hours = Math.floor((time / (1000 * 60 * 60)) % 24);
      const days = Math.floor(time / (1000 * 60 * 60 * 24));

      this.setState({ days, hours, minutes, seconds, time });
    }
  }
  render() {
    return (
      <Typography variant="h1" minWidth={800} textAlign="center">
        {this.leading0(this.state.days)} : {this.leading0(this.state.hours)} : {this.leading0(this.state.minutes)} : {this.leading0(this.state.seconds)}
      </Typography>
    );
  }
}

const useStyles = makeStyles({
  card: {
    padding: 140,
    backgroundColor: "rgba(50, 50, 50, 0)",
    color: "white",
    borderRadius: 50,
  },
  button: {
    minWidth: 200,
    borderRadius: 10,
    padding: 10,
  },
  matter: {
    color: "red",
    borderColor: "red",
    "&:hover": {
      borderColor: "red",
    },
  },
  antimatter: {
    color: "pink",
    borderColor: "pink",
    "&:hover": {
      borderColor: "pink",
    },
  },
});

export default function Countdown() {
  const [date, setDate] = useState(0);

  async function requestAccount() {
    await window.ethereum.request({ method: "eth_requestAccounts" });
  }

  const getDate = async () => {
    if (typeof window.ethereum === "undefined") {
      return;
    }

    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const contract = new ethers.Contract(contracts.matter, Matter.abi, provider);
    setDate(await contract.unlockTime());
  };

  const claimMatter = async () => {
    if (typeof window.ethereum === "undefined") {
      return;
    }

    await requestAccount();
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const contract = new ethers.Contract(contracts.matter, Matter.abi, signer);
    await contract.claim();
  };

  const claimAntiMatter = async () => {
    if (typeof window.ethereum === "undefined") {
      return;
    }

    await requestAccount();
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const contract = new ethers.Contract(contracts.antimatter, AntiMatter.abi, signer);
    await contract.claim();
  };

  const classes = useStyles();

  useEffect(() => {
    getDate();
  }, []);

  return (
    <Grid>
      <Card className={classes.card} raised>
        <Clock deadline={date * 1000} />
      </Card>
      <Grid container direction="row" justifyContent="space-around" alignItems="center">
        <Grid item>
          <Button variant="outlined" className={classnames(classes.button, classes.matter)} onClick={claimMatter}>
            Claim Matter
          </Button>
        </Grid>
        <Grid item>
          <Button variant="outlined" className={classnames(classes.button, classes.antimatter)} onClick={claimAntiMatter}>
            Claim AntiMatter
          </Button>
        </Grid>
      </Grid>
    </Grid>
  );
}
